import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {TaskDisplayTypesComponent} from './task-display-types/task-display-types.component';
import {ApprovalsComponent} from './approvals/approvals.component';
import {ApprovalRequestComponent} from './approval-request/approval-request.component';
import {RouterModule} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {LoaderModule} from '../../shared/loader/loader.module';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';

export const routes = [
    {path: 'approvals', component: ApprovalsComponent},
    {path: 'approval-requests', component: ApprovalRequestComponent},
    {path: 'task-display-types', component: TaskDisplayTypesComponent},
];

@NgModule({
    declarations: [TaskDisplayTypesComponent, ApprovalsComponent, ApprovalRequestComponent],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        FormsModule,
        ReactiveFormsModule,
        LoaderModule,
        CustomErrorModule
    ]
})
export class WorkflowModule {
}
